@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,600;1,700&display=swap);
.country{width:75vw;margin:0 auto;display:flex;justify-content:space-between;align-content:center;align-items:center;padding:20px;border:1px solid #cecece;border-right:none;border-left:none}.country__name{font-weight:bold}.country__details{display:inherit}.country__details-data{margin-right:20px;display:flex;align-items:center;align-items:center}.country__details-data img{width:30px;height:30px;margin-right:5px}@media screen and (max-width: 412px){.country{width:100vw;padding:20px 2px;flex-direction:column}.country__name{font-weight:bold;font-size:26px;margin-bottom:10px;align-self:flex-start}.country__details{display:inherit;font-size:18px}.country__details-data{margin-right:8px;display:flex;align-items:center;align-items:center}.country__details-data img{width:20px;height:20px;margin-right:5px}}

.select{width:300px;display:inline-block;padding:10px 5px 10px 5px;border:1px solid #cecece;font-weight:500}

*{font-family:"Zilla Slab", serif;margin:0}

